.eyes
{
    position: relative;
    display: flex;
    gap: 1em;
}

.eye
{
    position: relative;
    /* border: 1px solid black; */
    width: 50px;
    height: 50px;
    border-radius: 50%;
    clip-path: ellipse(100% 42% at 50% 50%);
    background: radial-gradient(ellipse at bottom right, white, #efdebf, #f3bf5e);
    box-shadow: inset -2px -2px 6px rgba(128, 26, 1, 0.8);
    overflow: hidden;
}

.eye.surprised
{
    clip-path: none;
}

.eye.angry
{
    clip-path: polygon(0% 10%, 100% 30%, 100% 80%, 0 90%, 0 0);
}

.eye.angry.right
{
    clip-path: polygon(0% 30%, 100% 10%, 100% 90%, 0 80%, 0 0);
}

.eye.sad
{
    clip-path: polygon(0% 30%, 100% 10%, 100% 80%, 0 80%, 0 0);
}

.eye.sad.right
{
    clip-path: polygon(0% 10%, 100% 30%, 100% 80%, 0 80%, 0 0);
}

.eye.happy
{
    clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%, 0 0);
}

.eye.happy.right
{
    clip-path: polygon(0 0, 100% 0, 100% 100%, 0 80%, 0 0);
}



.eye > .inner
{
    position: absolute;
    /* top: 20%;
    left: 20%; */
    width: 100%;
    height: 100%;
}

.eye .iris
{
    position: relative;
    top: calc(50% - 60% / 2);
    left: calc(50% - 60% / 2);
    width: 60%;
    height: 60%;
    border-radius: 50%;
    opacity: 0.7;
    background: radial-gradient(circle at bottom right, cornflowerblue 40%, #070776);
    outline: 1px solid rgba(0, 0, 0, 0.7);
}

.eye .pupil
{
    position: absolute;
    top: calc(50% - 25% / 2);
    left: calc(50% - 25% / 2);
    width: 25%;
    height: 25%;
    border-radius: 50%;
    background-color: black;
}

.eye .highlight
{
    position: absolute;
    top: 10%;
    left: 15%;
    background-color: white;
    width: 40%;
    height: 12%;
    border-radius: 35%;
    transform: rotate(-30deg);
    opacity: 0.7;
    box-shadow: 0 0 0.25em 0.05em white;
}


.eye .debug
{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 100%;
    font-size: 0.8em;
}
